@import url("https://fonts.googleapis.com/css2?family=Inter:wght@100;200;300;400;500;600;700;800;900&display=swap");

#root {
  display: flex;
  flex-direction: column;
  min-height: 100vh;
}
main {
  padding-top: 35px;
  flex: 1 0 auto;
}


@media screen and (max-width: 640px) {
  .main {
    padding: 0;
  }
}

/* Tailwind Styles */

.black_btn {
  @apply rounded-full border border-black bg-black py-1.5 px-5 text-white transition-all hover:bg-white hover:text-black text-center text-sm font-[inter] flex items-center justify-center;
}

.outline_btn {
  @apply rounded-sm border border-black bg-transparent py-1.5 px-5 text-black transition-all hover:bg-black hover:text-white text-center text-sm font-[inter] flex items-center justify-center;
}


.search_input {
  @apply block w-full rounded-l-sm border border-gray-200 bg-white py-2.5 pl-5 pr-12 text-sm shadow-lg font-medium focus:border-black focus:outline-none focus:ring-0;
}


/* PromptCard Component */
.prompt_card {
  @apply flex-1 break-inside-avoid rounded-sm border border-gray-200 bg-white/20 
  shadow-[-3px_1px_3px_0_rgba(0,0,0,0.2)] bg-clip-padding p-6 pb-4 backdrop-blur-lg backdrop-filter md:w-[360px] w-full h-fit;
}

.prompt_layout {
  @apply space-y-6 py-8 px-2 md:columns-2 sm:gap-6 xl:columns-3;
}

.flex-center {
  @apply flex justify-center items-center;
}

.flex-start {
  @apply flex justify-start items-start;
}

.flex-end {
  @apply flex justify-end items-center;
}

.flex-between {
  @apply flex justify-between items-center;
}